.review__top__row{
    display: flex;
    font-size: 68px;
    line-height: 1;
    color: #F4F4F4;
    margin-top: 4px;
    .stars{
        display: flex;
        align-items: flex-end;
        margin-left: 16px;
        margin-bottom: 10px;
        svg{
            margin-right: 5px;
        }
    }
}
.review__top{
    margin-bottom: 56px;
}
.reviews__list{
    border-top: 1px solid rgba($color: #fff, $alpha: 0.5);
    padding-top: 42px;
   
}
.reviews__list__flex{
    margin: 0 -50px;
    display: flex;
    flex-wrap: wrap;
}
.reviews__item{
    width: calc(100% / 2 - 100px);
    margin-left: 50px;
    margin-right: 50px;
    margin-bottom: 65px;
    display: flex;
    .stars{
        margin-bottom: 25px;
    }
}
.reviews__item__img{
    width: 105px;
    height: 105px;
    min-width: 105px;
    border-radius: 100%;
    background-size: cover;
    background-position: center;
    position: relative;
    margin-right: 29px;
    .big{
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        border-radius: 100%;
        overflow: hidden;
    }
    .img{
        position: absolute;
        right: 0;
        bottom: 0;
        width: 40px;
        height: 40px;
        border-radius: 100%;
        overflow: hidden;
    }
}
.reviews__item__top{
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    margin-bottom: 5px;
}
.revieww__item__name{
    font-size: 24px;
    line-height: 30px;
    color: #fff;
    margin-right: 35px;
    font-weight: bold;
    font-family: var(--gilroy);
}
.revieww__item__data{
    font-size: 16px;
    line-height: 19px;
    margin-top: 5px;
}
.reviews__item__des__text{
    font-size: 16px;
    line-height: 19px;
    color: #FFFFFF;
    margin-bottom: 7px;
    display: -webkit-box;
    -webkit-line-clamp: 5;
    -webkit-box-orient: vertical;
    text-overflow:ellipsis;
    overflow:hidden;
    max-width: 351px;
}
.reviews__item__des{
    .reviews__hide-more{
        display: none;
    }
}
.reviews__item__des.active{
    .reviews__item__des__text{
        display: block;
    }
    .reviews__hide-more{
        display: block;
    }
    .reviews__show-more{
        display: none;
    }
}

.pagination{
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 40px;
    border-top: 1px solid rgba($color: #fff, $alpha: 0.5);
}
.pagination__left{
    flex: 1;
    display: flex;
    margin-right: 15px;
    a{
        margin-right: 5px;
        padding: 5px;
    }
    a:hover,a.active{
        color: #fff;
    }
}
.pagination__arows{
    flex: 1;
    margin-left: 15px;
    display: flex;
    justify-content: flex-end;
    a{
        width: 40px;
        height: 40px;
        margin-left: 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: 0.3s all;
    }
    a:hover{
        background-color: rgba($color: #fff, $alpha: 0.1);
    }
}
.pagination__select{
    display: flex;
    justify-content: center;
}